.todoCard{cursor:pointer;display:flex;justify-content:space-between;align-items:center;margin:0 auto;width:90%;min-height:60px;border-radius:10px;padding:10px;background:rgba(81,81,81,.359);margin-bottom:10px;margin-top:5px;color:#fff}.todoCard .taskTitle{width:90%;height:100%;word-wrap:wrap}.todoCard .taskTitle span{margin-right:20px}.container{width:fit-content;align-items:center;display:flex;position:relative;padding-left:30px;margin-bottom:1px;margin-top:6px;cursor:pointer;font-size:22px;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}.container input{display:none}.checkmark{position:absolute;top:0;left:0;height:25px;width:25px;border-radius:50%;border:2px solid #52a0e9;background-color:none}.container input:checked~.checkmark{background-color:#52a0e9}.checkmark:after{content:"";position:absolute;display:none}.container input:checked~.checkmark:after{display:block}.container .checkmark:after{left:7px;top:4px;width:5px;height:10px;border:solid #fff;border-width:0 3px 3px 0;-webkit-transform:rotate(45deg);-ms-transform:rotate(45deg);transform:rotate(45deg)}.important{margin:0px 7px 25px 0px}.star{color:rgba(255,255,255,.588);visibility:hidden;font-size:30px;cursor:pointer}.star:before{content:"☆";visibility:visible}.star:checked:before{color:#44a8f6;content:"★"}.btns{display:flex}.removeBtn{margin:10px}.removeBtn button{padding:5px;color:red;background:none;font-size:1.1em;font-weight:700;border:none;cursor:pointer}